.loginbtn {
  width: 110px;
  height: 30px;

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  display: inline-block;

  text-align: center;
  align-items: center;
  letter-spacing: -0.015em;

  color: #73bd88;
  border: 1px solid #73bd88;
  border-radius: 6px;

  margin-right: 30px;
  margin-top: 30px;
  line-height: 28px;

  text-decoration: none;

